.button {
    background-color: #6278f7;
    border-radius: 10px;
    font-weight: 700;
    font-size: 18px;
    padding: 32px;
    border: none;
    cursor: pointer;
    color: white;
    margin: 32px 0 0;
    transition: 0.5s;
}

.button:hover {
    color: #c3ffb1;
    transition: 0.5s;
}