.collaborator {
    width: 280px;
}
.collaborator .header {
    background-color: #F0F0F0;
    border-radius: 10px 10px 0 0;
}
.collaborator img {
    width: 100px;
    border-radius: 50%;
    position: relative;
    top: 50px;
}
.collaborator .footer {
    background: #FFFFFF;
    box-shadow: 5px 5px 15px rgba(0, 0, 0, 0.08);
    border-radius: 0 0 10px 10px;
    padding-top: 90px;
    padding-bottom: 40px;
}
.collaborator .footer h4 {
    color: #6278F7;
    font-size: 18px;
    line-height: 22px;
    font-weight: bold;
    margin-bottom: 8px;
}
.collaborator .footer h5 {
    font-size: 18px;
    line-height: 22px;
    color: #212121;
    padding: 0 16px;
}